"use client";

import React, { useEffect, useState } from 'react';
import { useSearchParams } from 'next/navigation';
import { useAppContext } from '../../lib/AppContext';
import { motion } from 'framer-motion';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Progress } from '@/components/ui/progress';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue
} from '@/components/ui/select';
import { Separator } from '@/components/ui/separator';
import { Save, Sparkles } from 'lucide-react';

export default function EditorPage() {
  const searchParams = useSearchParams();
  const documentId = searchParams.get('documentId');
  const { getDocumentPdfUrl } = useAppContext();

  const [pdfUrl, setPdfUrl] = useState<string | null>(null);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    if (!documentId) {
      setError("No document ID provided.");
      return;
    }
    setIsLoading(true);
    setError(null);
    try {
      const url = getDocumentPdfUrl(documentId);
      if (!url) {
        setError("PDF not found for this document.");
      } else {
        setPdfUrl(url);
      }
    } catch (e) {
      setError("Unable to load PDF.");
    } finally {
      setIsLoading(false);
    }
  }, [documentId, getDocumentPdfUrl]);

  if (isLoading) {
    return (
      <div className="flex flex-col items-center justify-center h-screen bg-zinc-50 dark:bg-zinc-950 text-center px-6">
        <Card className="w-full max-w-md border-zinc-200 dark:border-zinc-800 bg-white dark:bg-zinc-900">
          <CardHeader>
            <CardTitle className="text-gray-900 dark:text-gray-50">Loading Documentâ€¦</CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            <Progress value={66} />
            <p className="text-sm text-gray-600 dark:text-gray-400">Preparing the viewer.</p>
          </CardContent>
        </Card>
      </div>
    );
  }

  if (error) {
    return (
      <div className="flex flex-col items-center justify-center h-screen bg-zinc-50 dark:bg-zinc-950 text-center text-red-600 dark:text-red-400">
        <h2 className="text-2xl font-semibold">Error: {error}</h2>
        <p className="text-gray-600 dark:text-gray-400 mt-2">Please go back and try again.</p>
      </div>
    );
  }

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
      className="min-h-screen bg-zinc-50 dark:bg-zinc-950 p-4 sm:p-6 lg:p-8 flex justify-center"
    >
      <div className="max-w-7xl w-full grid grid-cols-1 md:grid-cols-3 gap-6">
        <div className="md:col-span-2">
          <Card className="border-zinc-200 dark:border-zinc-800 bg-white dark:bg-zinc-900">
            <CardHeader className="pb-2">
              <CardTitle className="text-gray-900 dark:text-gray-50">Document Viewer</CardTitle>
            </CardHeader>
            <CardContent className="p-0">
              {pdfUrl ? (
                <iframe
                  src={pdfUrl}
                  className="w-full h-[88vh] rounded-b-xl"
                  title="PDF Viewer"
                />
              ) : (
                <div className="p-6 text-sm text-gray-600 dark:text-gray-400">
                  No PDF available to display.
                </div>
              )}
            </CardContent>
          </Card>
        </div>

        <aside className="md:col-span-1">
          <Card className="border-zinc-200 dark:border-zinc-800 bg-white dark:bg-zinc-900">
            <CardHeader className="pb-3">
              <CardTitle className="text-teal-600 dark:text-teal-400">
                Help us understand your document
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="projectName">Name this project</Label>
                <Input id="projectName" placeholder="Enter Project Name" />
              </div>

              <div className="space-y-2">
                <Label>Document type</Label>
                <Select>
                  <SelectTrigger>
                    <SelectValue placeholder="Select document type" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="factsheet">Factsheet</SelectItem>
                    <SelectItem value="prospectus">Prospectus</SelectItem>
                    <SelectItem value="email">Email</SelectItem>
                    <SelectItem value="ad">Ad/Marketing</SelectItem>
                  </SelectContent>
                </Select>
              </div>

              <div className="space-y-2">
                <Label htmlFor="title">Title of the Piece</Label>
                <Input id="title" placeholder="Enter title" />
              </div>

              <div className="text-xs text-zinc-600 dark:text-zinc-400">
                {pdfUrl ? `File: ${decodeURIComponent(pdfUrl.split('/').pop() || 'document.pdf')}` : 'No file selected'}
              </div>

              <Separator />

              <div className="grid grid-cols-2 gap-3">
                <div className="space-y-2">
                  <Label htmlFor="firstUse">Anticipated first use date</Label>
                  <Input id="firstUse" type="date" />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="asOf">"As Of" date of content</Label>
                  <Input id="asOf" type="date" />
                </div>
              </div>

              <div className="space-y-2">
                <Label htmlFor="frequency">How often the content is updated</Label>
                <Input id="frequency" placeholder="e.g., Monthly, Quarterly" />
              </div>

              <div className="flex items-center gap-3 pt-2">
                <Button variant="outline" className="w-full">
                  <Save className="h-4 w-4 mr-2" />
                  Save as draft
                </Button>
                <Button className="w-full">
                  <Sparkles className="h-4 w-4 mr-2" />
                  PO2 Compliance Check
                </Button>
              </div>
            </CardContent>
          </Card>
        </aside>
      </div>
    </motion.div>
  );
}
